@import "../../_variables.scss";

/* Making the footer work */
div#root {
  position: relative;
  min-height: 100vh;
  > #footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: auto;
    text-align: center;
    > nav {
      padding: 0.5rem;
    }
  }
}